@import "~Common/constants.css";

.SelectDialog {
  width: 75vw;
  height: 91vh;
  background-color: var(--white-brown);
  z-index: 100;
  overflow: hidden;
  border-radius: 15px;
  animation: dialog-opening 1s ease 1;
}

@keyframes dialog-opening {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

.Header {
  background-color: var(--dark-brown);
  height: 8vh;
  width: 75vw;
  color: #eac18f;
  font-size: 6vh;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
