.RoundWrapper {
  /* background-color: #b1663b; */
  background-color: #975631;
  border-radius: 15px;
  width: 100%;
  height: 100%;
  box-shadow: 4px 4px 5px 0px rgb(53 48 42) inset;
  display: flex;
  justify-content: center;
  align-items: center;
}

.RoundWrapper1 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Round {
  border-radius: 50%;
  width: 90%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Wrapper {
  position: relative;
  margin: 2vh;
  width: 22vh;
  height: 22vh;
}

.Count {
  position: absolute;
  bottom: 1vh;
  right: 1vh;
  color: aliceblue;
  font-family: serif;
  font-size: 6vh;
}
