@import "~Common/constants.css";

.SelectDialog {
  width: 75vw;
  height: 91vh;
  background-color: #b1663b;
  z-index: 1000000;
  overflow: hidden;
  border-radius: 15px;
  animation: dialog-opening 0.3s ease-in-out 1;
}

@keyframes dialog-opening {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

.Header {
  background-color: var(--dark-brown);
  height: 8vh;
  width: 75vw;
  color: #eac18f;
  font-size: 6vh;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.Wrapper {
  display: flex;
  height: 100%;
  margin-top: 5vh;
}

.CardOptionWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ButtonsWrapper {
  display: flex;
  justify-content: center;
}

.Cancel {
  position: fixed;
  bottom: 7vh;
  width: 26vw;
  background-color: var(--dark-brown);
  color: var(--gold);
  font-size: 6vh;
  border: 2px solid var(--gold);
  border-radius: 5px;
  font-weight: 450;
}

.GameCards {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
