.Resources {
  width: 100%;
  height: 17vh;
  border: 2px solid #3f2e1b;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.res-box {
  display: flex;
  align-items: center;
  border: 2px solid #a55c31;
  box-shadow: 4px 4px 5px 0px rgb(53 48 42) inset;
}

.res-count {
  width: 14vw;
  font-size: 7vh;
  height: 7vh;
  background-color: #3f2e1b;
  font-family: serif;
  color: #eac18f;
  border: 3px solid #3f2e1b;
  border-right: 2px solid #a55c31;
}

.long-count {
  display: flex;
  border-left: 6px solid #3f2e1b;
}

.ResoursesCount {
  width: 100%;
  height: 7vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.Inventory {
  height: 100%;
  overflow-y: hidden;
  border: 6vh solid #b1663b;
  background-color: #221714;
}
