.MainMenu {
  background-color: #683a20;
  width: 30vw;
  height: 100vh;
}

.Link_active {
  background-color: #dc932d !important;
  color: #3f2e1b !important;
}

.Link {
  text-decoration: none;
  color: #3f2e1b;
  background-color: #3f2e1b;
}

.tabs {
  display: flex;
  flex-direction: column;
  height: 85vh;
}

.tab {
  color: #eac18f;
  font-size: 6vh;
  margin: 0 auto;
  margin-top: 4vh;
  width: 80%;
  height: 8vh;
  border: 2px solid #3f2e1b;
}

.tab:hover {
  background-color: #dc932d;
  color: #3f2e1b;
}

.tab.active {
  background-color: #dc932d;
  color: #3f2e1b;
}

.avatar__img {
  width: 13vw;
  height: 13vw;
}

.AvatarBox {
  width: 20vw;

  margin: 0 auto;
  margin-top: 3vh;
  margin-bottom: 1.5vh;
}

.NickName {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18vw;
  height: 7vh;
  text-align: center;
  margin: 0 auto;
  font-size: 7vh;
  background-color: #eac18f;
  text-overflow: clip;
  border: 3px solid #3f2e1b;
  color: #3f2e1b;
}

.StepButtonBox {
  height: 15vh;
}

.StepButton {
  width: 12vw;
  font-size: 6vh;
  margin: 0 auto;
  margin-top: 3vh;
  border: 2px solid #dc932d;
  border-radius: 5px;
  background-color: #3f2e1b;
  color: #dc932d;
}

.Runes {
  margin: 0 auto;
  margin-top: 4vh;
  height: 15vh;
}

.RuneImg {
  width: 12vh;
  height: 12vh;
}
