.MenuWrapper {
  display: flex;
  flex-direction: column;
}

.MenuItem {
  position: absolute;
  border: 2px solid #848484;
  z-index: 1000;
  background-color: #3f2e1b;
  color: #eac18f;
}

.EmergenceLeft {
  animation: menuEmergenceLeft 1s ease;
}

.EmergenceRight {
  animation: menuEmergenceRight 1s ease;
}

@keyframes menuEmergenceLeft {
  0% {
    opacity: 0;
    transform: translateX(-10%);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes menuEmergenceRight {
  0% {
    opacity: 0;
    transform: translateX(+10%);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
