.mainBox {
  display: flex;
  flex-direction: row;
}

.hunger {
  position: relative;
}

.hunger:before {
  content: "";
  position: absolute;
  top: 3%;
  left: 3%;
  width: 85%;
  height: 110%;
  border-radius: 10%;
  background-color: rgba(228, 140, 89, 0.35);
  z-index: 9;
}

.hunger:after {
  content: "";
  background-image: url("./images/hunger.svg");
  background-repeat: no-repeat;
  position: absolute;
  top: 40%;
  left: 25%;
  z-index: 10;
  width: 40%;
  height: 40%;
  animation: hunger-animation 1s linear infinite;
}

@keyframes hunger-animation {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes select {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(0);
  }
}
