@import "~Common/constants.css";
.CardCreationDialogWrapper {
  background-color: #b1663b;
  width: 75vw;
  height: 86vh;
  border-radius: 15px;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: dialog-opening 1s ease 1;
}

@keyframes dialog-opening {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

.Header {
  background-color: var(--dark-brown);
  height: 8vh;
  width: 75vw;
  color: #eac18f;
  font-size: 6vh;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.Main {
  width: 70vw;
  height: 64vh;
  display: flex;
}

.CardContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 63vh;
}

.ButtonsWrapper {
  display: flex;
  justify-content: center;
  margin-top: 2vh;
}

.Cancel {
  width: 22vw;
  background-color: var(--dark-brown);
  color: var(--gold);
  font-size: 6vh;
  border: 2px solid var(--gold);
  border-radius: 5px;
  font-weight: 450;
}

.Cancel:hover {
  background-color: var(--white-brown);
  color: var(--dark-brown);
  font-weight: 500;
  border: 2px solid var(--dark-brown);
}

.Create {
  width: 22vw;
  background-color: var(--gold);
  color: var(--dark-brown);
  font-size: 6vh;
  font-weight: 500;
  border: 2px solid var(--dark-brown);
  border-radius: 5px;
}

.Create__disabled {
  width: 22vw;
  background-color: var(--gold__disabled);
  color: var(--dark-brown__disabled);
  font-size: 6vh;
  font-weight: 500;
  border: 2px solid var(--dark-brown__disabled);
  border-radius: 5px;
  opacity: 0.6;
}

.Create:hover {
  background-color: var(--white-brown);
  border: 2px solid var(--dark-brown);
}

.Gutter {
  width: 4vw;
}

.SumWrapper {
  width: 8vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.SumBottom {
  height: 10vh;
}

.ChangeButton {
  width: 17vw;
  background-color: #6f3d21;
  color: #eac18f;
  font-size: 6vh;
  border: 2px solid var(--dark-brown);
  border-radius: 5px;
  margin-top: 3vh;
}

.TargetCardWrapper {
  width: 35vw;
  display: flex;
  justify-content: space-evenly;
}

.TargetCardCreated {
  z-index: 100;
  animation: card-born 0.7s ease 1;
}

@keyframes card-born {
  0% {
    /* opacity: 0; */
    transform: translateX(100%);
  }
  100% {
    /* opacity: 1; */
    transform: translateX(0);
  }
}
